/* Modern Loading Spinner Styles */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
}

.loading-container.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  z-index: 9998;
  padding: 2rem;
}

.modern-spinner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.spinner-ring {
  position: absolute;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top: 3px solid;
  animation: spin 1.2s cubic-bezier(0.68, -0.55, 0.265, 1.55) infinite;
}

.spinner-ring:nth-child(1) {
  border-top-color: #667eea;
  animation-delay: 0s;
}

.spinner-ring:nth-child(2) {
  border-top-color: #667eea;
  animation-delay: 0.2s;
}

.spinner-ring:nth-child(3) {
  border-top-color: #f093fb;
  animation-delay: 0.4s;
}

/* Size variants */
.spinner-small .spinner-ring {
  width: 20px;
  height: 20px;
  border-width: 2px;
}

.spinner-medium .spinner-ring {
  width: 40px;
  height: 40px;
  border-width: 3px;
}

.spinner-large .spinner-ring {
  width: 60px;
  height: 60px;
  border-width: 4px;
}

/* Ring positioning for layered effect */
.spinner-small .spinner-ring:nth-child(2) {
  transform: scale(0.8);
}

.spinner-small .spinner-ring:nth-child(3) {
  transform: scale(0.6);
}

.spinner-medium .spinner-ring:nth-child(2) {
  transform: scale(0.85);
}

.spinner-medium .spinner-ring:nth-child(3) {
  transform: scale(0.7);
}

.spinner-large .spinner-ring:nth-child(2) {
  transform: scale(0.9);
}

.spinner-large .spinner-ring:nth-child(3) {
  transform: scale(0.8);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Loading message styles */
.loading-message {
  color: #4a5568;
  font-weight: 500;
  text-align: center;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.message-small {
  font-size: 0.8rem;
}

.message-medium {
  font-size: 1rem;
}

.message-large {
  font-size: 1.2rem;
}

/* Pulse animation for overlay */
.loading-container.overlay .loading-message {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

